/*------------------------------------*\
    #BUTTONS
\*------------------------------------*/

/**
 * Button
 */
.c-btn {
	height: 29px;
	width: 145px;
	display: inline-block;
	background: $color-brand-yellow;
	color: $color-neutral-grey3;
	font-weight: bold;
	font-size: 10pt;
	border: 2px;
	border-radius: $border-radius;
	text-align: center;
	transition: background 0.2s ease;

	&:hover, &:focus {
		background: $color-brand-yellow;
		color: $color-neutral-grey3;
		border: 1;
		border-color: $color-neutral-grey3;
	}
	&.disabled {
		background: $color-brand-yellow;
		color: $color-neutral-grey3;
	}
}

.c-btn1 {
	height: 45px;
	width: 175px;
	display: inline-block;
	background: $color-neutral-grey3;
	color: $color-brand-beige;
	font-weight: 700;
	font-size: 11pt;
	border: 0;
	padding : 5px 35px 5px 35px;

	border-radius: $border-radius;
	text-align: center;
	transition: background 0.2s ease;

	&:hover, &:focus {
		background: $color-neutral-grey3;
		color: $color-brand-yellow;
		border: 1;
//		border-color: $color-neutral-grey3;
	}
	&.disabled {
		background: $color-neutral-grey3;
		color: $color-brand-yellow;
	}
}


 .c-btn2 {
 	height: 45px;
 	width: 215px;
 	display: flex;
 	background: $color-brand-beige;
 	color: $color-neutral-grey3;
 	font-weight: bold;
 	font-size: 11pt;
 	border: 0;
 	padding : 5px 25px 5px 25px;
 	border-radius: $border-radius;
 	text-align: center;
 	transition: background 0.2s ease;

 	&:hover, &:focus {
 		background: $color-brand-yellow;
 		color: $color-neutral-grey3;
 		border: 1;
 //		border-color: $color-neutral-grey3;
 	}
 	&.disabled {
 		background: $color-brand-beige;
 		color: $color-neutral-grey3;
 	}
 }

/**
 * Large button
 */
.c-btn-large {
	padding: $pad-half;
	text-transform: uppercase;
	background: $color-gray-50;
	font-size: 1.4rem;
	font-weight: normal;
}

/**
 * Text button
 */
.c-text-btn {
	position: relative;
	background: none;
    border: none;
    font-style: normal;
    font-weight: bold;
    font-size: 1.2rem;

    &:after {
	    position: absolute;
		content: "";
		height: 20px;
		width: 20px;
		background: url("../../../images/icon-arrowhead.svg") no-repeat;
	    top: 7px;
		right: -26px;
		transition: all 0.2s ease;
	}

	&:hover, &:focus {
	    color: $color-gray-73;

	    &:after {
		    right: -28px;
	    }
    }

    .c-block-media--small & {
	    font-size: 1rem;
	}
}

/**
 * Button on overlay
 */

 .c-btn--overlay {
	 background: $color-gray-35;
 }
